/*10px = 1rem*/
html {
  font-size: 62.5%; }

body {
  font-size: 16px; }

h1, h2, ul, ol, li, a {
  margin: 0; }

body {
  font-family: "Manuale", serif; }

h3 {
  font-size: 3.3rem;
  text-align: center;
  margin: 2rem 0;
  text-transform: uppercase; }

h4 {
  font-size: 2.8rem;
  text-align: center;
  margin: 2rem 0;
  text-transform: uppercase; }

p, li {
  font-size: 2rem;
  line-height: 1.4; }

a {
  color: inherit;
  text-decoration: none; }

img {
  width: 100%; }

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto; }

body.noscroll {
  overflow: hidden; }

.header {
  position: fixed;
  z-index: 20;
  width: 100%;
