  background-color: #1c3d91;
  color: white;
  padding: .5rem 0; }
  @media (min-width: 480px) {
    .header {
      padding: 1.4rem 0; } }
  .header.below {
    z-index: 5; }
  .header .header__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem; }
    @media (min-width: 810px) {
      .header .header__content {
        display: flex;
        flex-direction: row;
        justify-content: space-between; } }
    .header .header__content h1 {
      display: flex;
      flex-direction: column;
      align-items: center;
      white-space: nowrap;
      font-size: 3.6rem; }
      @media (min-width: 480px) {
        .header .header__content h1 {
          display: block; } }
      .header .header__content h1:hover {
        color: #f72840; }
    .header .header__content .header__nav {
      font-size: 2.4rem;
      display: flex;
      flex-direction: column;
      gap: 1rem; }
      @media (min-width: 480px) {
        .header .header__content .header__nav {
          flex-direction: row;
          gap: 3rem; } }
      .header .header__content .header__nav a {
        text-align: center; }
      .header .header__content .header__nav a:hover {
        font-weight: 700;
        color: #f72840; }
      .header .header__content .header__nav a:before {
        display: block;
        content: attr(title);
        font-weight: 700;
        height: 0;
        overflow: hidden;
        visibility: hidden; }

.header__fill {
  height: 20.3rem; }
  @media (min-width: 480px) {
    .header__fill {
      height: 10.6rem; } }
  @media (min-width: 810px) {
    .header__fill {
      height: 6.8rem; } }

.video {
  position: relative;
  max-height: 50rem;
  overflow: hidden; }
  .video .video__content {
    z-index: 1;
    color: white;
    position: absolute;
    background: linear-gradient(150deg, rgba(255, 78, 78, 0.55) -10%, rgba(47, 66, 153, 0.6) 40%, rgba(47, 66, 153, 0.6) 60%, rgba(255, 255, 255, 0.55) 110%);
    height: 100%;
    width: 100%; }
    .video .video__content .video__text {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center; }
      @media (min-width: 480px) {
        .video .video__content .video__text {
          justify-content: flex-start; } }
    .video .video__content h2 {
      font-size: 6.5vw;
      font-weight: 400;
      text-shadow: 0.3rem 0.3rem 0.2rem rgba(25, 26, 61, 0.493);
      text-transform: capitalize;
      display: flex;
      flex-direction: column;
      align-items: center; }
      @media (min-width: 480px) {
        .video .video__content h2 {
          font-size: 4.5vw;
          align-items: unset; } }
      @media (min-width: 1200px) {
        .video .video__content h2 {
          font-size: 5.5rem; } }
      .video .video__content h2 span {
        font-size: 1.5em;
        font-weight: 700;
        text-transform: uppercase;
        color: #f72840; }
        .video .video__content h2 span:before {
          content: '';
          display: inline-block;
          height: 1em;
          width: 1em;
          margin: 0 1rem 0 0;
          background-repeat: no-repeat;
          background-size: 1em;
          background-position: center bottom; }
          .webp .video .video__content h2 span:before {
            background-image: url(../img/nz-flag.webp); }
          .no-webp .video .video__content h2 span:before {
            background-image: url(../img/nz-flag.png); }
          @media (min-width: 480px) {
            .video .video__content h2 span:before {
              margin: 0 2%; } }
  .video video {
    display: block;
    width: 100%; }

.introduction {
  margin: 4rem auto; }

.introduction__content {
  display: grid;
  grid-template-rows: auto auto;
  column-gap: 3rem; }
  .introduction__content picture {
    margin: auto 0; }
    .introduction__content picture img {
      border-radius: .8rem; }
  .introduction__content div {
    margin: auto 0; }
    .introduction__content div p {
      line-height: 1.8; }
  @media (min-width: 768px) {
    .introduction__content {
      grid-template-rows: auto;
      grid-template-columns: 1fr 1fr; } }

.places-bg {
  background-color: #fd808e;
  padding: 4rem 0; }
  .places-bg h3 {
    margin-top: 0;
    color: white; }

.places {
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem; }
  @media (min-width: 480px) {
    .places {
      flex-direction: row;
      align-items: unset;
      gap: 2.5rem; } }
  @media (min-width: 768px) {
    .places {
      gap: 4rem; } }
  .places .places__list {
    width: 100%;
    padding: 2rem 0;
    border-radius: 1.2rem; }
    @media (min-width: 480px) {
      .places .places__list {
        max-width: 45rem;
        width: 50%; } }
  .places h4 {
    margin: 0 0 1rem 0; }
    .places h4:after {
      content: '';
      display: block;
      margin: 1rem auto;
      width: 10rem;
      height: 10rem;
      background-color: rgba(255, 255, 255, 0.55);
      border-radius: 2rem;
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center; }
  .places ul {
    padding: 0;
    list-style: none; }

.places__list-city {
  background-color: #1c3d91; }
  .webp .places__list-city h4:after {
    background-image: url(../img/icon-city.webp); }
  .no-webp .places__list-city h4:after {
    background-image: url(../img/icon-city.png); }
  .places__list-city li {
    padding: 1rem 4rem;
    text-transform: capitalize; }
    .places__list-city li:nth-child(odd) {
      background-color: #3452a0; }

.places__list-natural {
  background-color: #3452a0; }
  .webp .places__list-natural h4:after {
    background-image: url(../img/icon-forest.webp); }
  .no-webp .places__list-natural h4:after {
    background-image: url(../img/icon-forest.png); }
  .places__list-natural li {
    padding: 1rem 4rem;
    text-transform: capitalize; }
    .places__list-natural li:nth-child(odd) {
      background-color: #1c3d91; }

.gallery-bg {
  padding: 4rem 0; }

.gallery h3 {
  margin: 0 0 2rem 0; }

.gallery .gallery__pictures {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem; }
  @media (min-width: 768px) {
    .gallery .gallery__pictures {
      grid-template-columns: repeat(5, 1fr); } }
  .gallery .gallery__pictures img {
    border-radius: .5rem; }

.imgBig {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.86);
  padding-top: 1rem;
  display: grid;
  grid-template-rows: 85vh 15vh;
  place-items: center; }

.imgBig__top {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 85% 15%;
  grid-template-areas: "image image" "Larrow Rarrow";
  place-items: center;
  row-gap: 2rem; }
  @media (min-width: 480px) {
    .imgBig__top {
      display: grid;
      grid-template-columns: 10% 80% 10%;
      grid-template-rows: unset;
      grid-template-areas: unset;
      width: 100%;
      height: 100%;
      padding: 0; } }

.imgBig__image {
  grid-area: image;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }
  @media (min-width: 480px) {
    .imgBig__image {
      grid-area: unset; } }
  .imgBig__image img {
    width: auto;
    max-width: 100%;
    height: auto;
    max-height: 80vh;
    padding-top: 4rem;
    border-radius: 0;
    cursor: pointer; }
    @media (min-width: 480px) {
      .imgBig__image img {
        max-height: calc(85vh - 1rem);
        padding: 0;
        border-radius: .5rem;
        border: 0.5rem solid rgba(255, 255, 255, 0.55); } }

.imgBig__leftArrow, .imgBig__rightArrow, .imgBig__bot {
  background-color: rgba(255, 255, 255, 0.425);
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  margin: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 2.5rem;
  justify-content: center;
  font-family: Helvetica, sans-serif;
  font-weight: 700; }

.packages-bg {
  background-color: #3a5cb3;
  padding: 4rem 0; }

.packages h3 {
  color: white;
  margin: 0 0 2rem 0; }

.packages .tables {
